import React from "react"
import "./Card.scss"
import { Paper } from "@mui/material"
function Card(props) {
  const { gap, cardData, col } = props

  return (
    <div className={`row g-${gap} card-icon`}>
      {cardData.map((card, i) => {
        return (
          <div className={`col-md-${col}`} key={i}>
            {/* <div className="col-md-6 col-sm-6 col-12"> */}
            <Paper className="card" elevation={0}>
              {!!card.img ? (
                <div className="icon-img text-center">
                  <div className="img-wrapper">
                    <img src={card.img} alt="img" />
                  </div>
                </div>
              ) : null}
              {!!card.header ? (
                <div className="heading">{card.header}</div>
              ) : null}
              {!!card.text ? <div className="text">{card.text}</div> : null}
            </Paper>
          </div>
        )
      })}
    </div>
  )
}

export default Card
